B,aAAa;gBACb,iBAAiB;gBACjB,iBAAiB;gBACjB,eAAe;gBACf,kBAAkB;gBAClB,aAAa;aAChB;;YAED;gBACI,WAAW;gBACX,aAAa;gBACb,mBAAmB;aACtB;;YAED;gBACI,gBAAgB;gBAChB,oBAAoB;gBACpB,uBAAuB;gBACvB,0BAA0B;gBAC1B,8BAA8B;gBAC9B,cAAc;gBACd,8DAA8D;aACjE;YACD,8EAA8E;;YAE9E;gBACI,yBAAyB;aAC5B;;aAEA;gBACG,yBAAyB;aAC5B;;YAED;gBACI,yBAAyB;aAC5B;;YAED;gBACI,yBAAyB;aAC5B;;YAED;gBACI,oBAAoB;aACvB;;YAED;gBACI,oBAAoB;aACvB;YACD,4EAA4E;YAC5E,gBAAgB;;YAEhB;gBACI,YAAY;aACf;;aAEA;gBACG,8BAA8B;aACjC;YACD,6CAA6C;;YAE7C;gBACI,yBAAyB;aAC5B;;YAED;gBACI,gBAAgB;gBAChB,YAAY;gBACZ,UAAU;gBACV,WAAW;aACd;;YAED;gBACI,aAAa;aAChB;;YAED;gBACI,UAAU;aACb;;YAED;gBACI,WAAW;aACd;;YAED;gBACI,iBAAiB;aACpB;;YAED;gBACI,eAAe;aAClB;;YAED;gBACI,eAAe;gBACf,mBAAmB;aACtB;YACD,eAAe;;YAEf;gBACI,mBAAmB;gBACnB,sBAAsB;gBACtB,YAAY;gBACZ,aAAa;gBACb,iBAAiB;gBACjB;oBACI;;wBAEI,kBAAkB;qBACrB;iBACJ;gBACD;;oBAEI,YAAY;oBACZ,mBAAmB;oBACnB,YAAY;oBACZ,YAAY;oBACZ,SAAS;oBACT,QAAQ;oBACR,iBAAiB;oBACjB,iBAAiB;iBACpB;gBACD;oBACI,kCAAkC;iBACrC;gBACD;oBACI,mCAAmC;iBACtC;gBACD;oBACI,6BAA6B;iBAChC;gBACD;oBACI;;wBAEI,YAAY;qBACf;iBACJ;gBACD;oBACI;;wBAEI,YAAY;wBACZ,iBAAiB;qBACpB;iBACJ;gBACD;oBACI;;wBAEI,YAAY;wBACZ,iBAAiB;qBACpB;iBACJ;gBACD;oBACI;;wBAEI,aAAa;wBACb,iBAAiB;qBACpB;iBACJ;gBACD;oBACI;;wBAEI,YAAY;wBACZ,WAAW;qBACd;iBACJ;gBACD;oBACI;;wBAEI,mBAAmB;qBACtB;iBACJ;gBACD;oBACI;;wBAEI,qBAAqB;qBACxB;iBACJ;gBACD;oBACI;;wBAEI,sBAAsB;qBACzB;iBACJ;gBACD;oBACI;;wBAEI,oBAAoB;qBACvB;iBACJ;aACJ","file":"player.css","sourcesContent":["            .fjfigure {\n                width: 100%;\n                height: auto;\n                margin: 0 0 0;\n                background-color: rgb(0, 0, 0);\n            }\n            \n            .fjPlayer {\n                background-color: rgba(0, 0, 0, 0.5);\n                color: rgb(255, 255, 255);\n                text-align: center;\n                position: relative;\n                margin: 0px auto 0px auto;\n                display: inline-block;\n                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n            }\n            \n            .fjplayerExpand {\n                width: 100%;\n            }\n            \n            .divofvideo {\n                -webkit-background-size: cover;\n                -moz-background-size: cover;\n                -o-background-size: cover;\n                background-size: cover;\n            }\n            /* ---------------------- Thumbs style --------------------- */\n            \n            .thumbsBlockDiv {\n                position: absolute;\n                display: table;\n                visibility: hidden;\n                border-radius: 2px;\n                z-index: 2147483647;\n                /*background: rgba(0, 0, 0, 0.5);*/\n                background: rgba(10, 10, 10, 0.8);\n                font-size: 12px;\n                border-radius: 5px;\n            }\n            \n            .thumbsBlock {\n                display: table-cell;\n                border-radius: 2px;\n                vertical-align: top;\n                display: inline-block;\n            }\n            /*______________________________ CONTROLS __________________________*/\n            \n            .divcontrols {\n                margin: 0px auto 0px auto;\n                width: 100%;\n                height: 25px;\n                position: absolute;\n                visibility: visible;\n                bottom: 0px;\n                left: 0px;\n            }\n            \n            .divcontrolsProgress {\n                margin: 0px auto 0px auto;\n                width: 100%;\n                height: 8px;\n                position: absolute;\n                visibility: visible;\n                bottom: 25px;\n                left: 0px;\n            }\n            \n            .divconeontrolLeft {\n                float: left;\n            }\n            \n            .divconeontrolRight {\n                float: right;\n            }\n            \n            .divconeontrolIcon {\n                width: 30px;\n            }\n            \n            .divconeontrolVBar {\n                width: 60px;\n            }\n            \n            .divonecontrol {\n                height: 25px;\n                padding-top: 2px;\n                padding-right: 1px;\n            }\n            \n            .divTitle,\n            .divcontrols,\n            .control,\n            .divonecontrol {\n                display: inline-block;\n                vertical-align: top;\n            }\n            /* this rule is for IE7 and below only\n put it in a Conditional comment without the !ie7 hack if you wish! */\n            \n            .divcontrols,\n            .control,\n            .divonecontrol {\n                display: inline !ie7;\n                margin: 0 2px !ie7;\n            }\n            \n            .controlText {\n                padding-top: 2px;\n                font-size: 0.75em;\n                white-space: nowrap;\n            }\n            \n            .divInfo {\n                z-index: 2147483647;\n                border-radius: 5px;\n                top: 10%;\n                width: 80%;\n                position: absolute;\n                left: 10%;\n            }\n            \n            .divBigPlayBtn {\n                font-size: 3.2em;\n                z-index: 2147483647;\n                border-radius: 5px;\n                top: 35%;\n                position: absolute;\n                left: 45%;\n                background-color: rgba(0, 0, 0, 0.5);\n                border-radius: 2%;\n                padding: 2%;\n            }\n            \n            .divBigPlayBtn:hover {\n                background: #333;\n            }\n            \n            .divIconBtn {\n                position: relative;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                white-space: nowrap;\n                display: inline-block;\n                cursor: pointer;\n                opacity: 0.8;\n            }\n            \n            .divIconBtn:hover {\n                opacity: 1.0;\n                background: #333;\n            }\n            \n            .divTspanitleSeparator {\n                position: relative;\n                visibility: visible;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                margin: 0px;\n                white-space: nowrap;\n                display: inline-block;\n                max-width: 766px;\n                font-size: 12px;\n                padding: 10px;\n            }\n            /*_________________________  Progress  ________________________*/\n            \n            .progressbar {\n                position: relative;\n                visibility: visible;\n                width: 100%;\n                display: inline-block;\n                cursor: pointer;\n                height: 10px;\n                padding-top: 2px;\n                padding-right: 1px;\n                bottom: 10px;\n            }\n            \n            .volumebar {\n                margin-top: 2%;\n                padding-left: 1%;\n                padding-right: 3%;\n                border-left: none;\n                border-right: none;\n            }\n            /*_________________________ RANGE ________________________*/\n            \n            input[type=\"range\"] {\n                -webkit-appearance: none;\n                -moz-apperance: none;\n                cursor: pointer;\n                width: 98%;\n                left: 1%;\n                border-radius: 6px;\n                height: 8px;\n                background-image: -webkit-gradient( linear, left top, right top, color-stop(0, #FF0000), color-stop(0, #8F9B9E));\n            }\n            \n            input[type='range']::-webkit-slider-thumb {\n                -webkit-appearance: none !important;\n                background-color: #FF0000;\n                border: 1px solid rgba(10, 10, 10, 0.8);\n                height: 14px;\n                width: 14px;\n                margin-left: -1.5px;\n                border-radius: 100%;\n                box-shadow: 2px 2px rgba(0, 0, 0, 0.5);\n            }\n            \n            input[type='range']::-moz-slider-thumb {\n                -webkit-appearance: none !important;\n                background-color: #FF0000;\n                border: 1px solid rgba(10, 10, 10, 0.8);\n                height: 14px;\n                width: 14px;\n                margin-left: -1.5px;\n                border-radius: 100%;\n                box-shadow: 2px 2px rgba(0, 0, 0, 0.5);\n            }\n            \n            input[type=range]:focus {\n                outline: none;\n            }\n            /* -------------------  CC SUBTITLES --------------------- */\n            \n            .subtitles-menu-item-actif {\n                cursor: pointer;\n                text-align: left;\n                border-radius: 2px;\n                opacity: 1;\n            }\n            \n            .subtitles-menu-item {\n                cursor: pointer;\n                text-align: left;\n            }\n            \n            .fj-hide {\n                visibility: hidden;\n                opacity: 0;\n                transition: visibility 200ms, opacity 200ms;\n            }\n            \n            .fj-show {\n                visibility: visible;\n            }\n            /* animation */\n            \n            .m-fadeOut {\n                visibility: hidden;\n                opacity: 0;\n                transition: visibility 200ms, opacity 200ms;\n            }\n            \n            .m-fadeIn {\n                visibility: visible;\n                opacity: 1;\n            }\n            /* ---------------------- Menu style --------------------- */\n            \n            .settingMenuDiv {\n                position: absolute;\n                background: rgba(10, 10, 10, 0.8);\n                float: left;\n                font-size: 12px;\n                /* visibility: hidden;*/\n                padding: 5px;\n                border-radius: 5px;\n                z-index: 2147483647;\n            }\n            \n            ul {\n                padding: 2px;\n                margin: 2px;\n                list-style-type: none;\n            }\n            \n            ul li {}\n            \n            .settingMenuSubMenuRight {\n                float: left;\n                width: 100%;\n                border-left: 1px solid #2A2A2A;\n                padding-left: 5px;\n                padding-right: 5px;\n            }\n            /* CUES and subtitles */\n            \n             ::cue {\n                color: #ccc;\n            }\n            \n             ::cue(v[voice='Test']) {\n                color: #fff;\n                background: #0095dd;\n            }\n            /* OVERLAYS */\n            \n            .fjOvcontainer {\n                width: 100%;\n                height: 100%;\n                margin: 0 auto;\n                padding: 0px;\n                /*position: relative;*/\n                position: absolute;\n            }\n            \n            .over-HL {\n                position: absolute;\n                background: rgba(0, 0, 0, 0.5);\n                float: left;\n                font-size: 12px;\n                padding: 5px;\n                border-radius: 5px;\n                z-index: 2147483647;\n                /****/\n                top: 20%;\n                left: 20%;\n                height: auto;\n            }\n            \n            .over-DL {\n                position: absolute;\n                background: rgba(0, 0, 0, 0.5);\n                float: center;\n                font-size: 12px;\n                border-radius: 5px;\n                z-index: 2147483647;\n                bottom: 2%;\n                width: 96%;\n                left: 2%;\n                height: auto;\n            }\n            /*__________________________________________ FJCONTROL ____________________________*/\n            \n            .fjcontrols-panel {\n                z-index: 2147483647;\n                position: absolute;\n                border-radius: 5px;\n                bottom: 3%;\n                opacity: 1;\n                background: rgba(10, 10, 10, 0.8);\n                width: 90%;\n                margin-left: 5%;\n                display: relative;\n                float: left;\n            }\n            \n            .fjcontrols-panel-controls {\n                width: 100%;\n                position: relative;\n                margin-bottom: 2px;\n            }\n            \n            .fjcontrols-control {\n                margin-bottom: 2px;\n                position: relative;\n                float: left;\n                height: 100%;\n                /*border-right: 1px solid #2A2A2A;*/\n            }\n            \n            .fjcontrols-control-right {\n                float: right;\n                /*  border-left: 1px solid #2A2A2A; */\n                border-right: none;\n            }\n            \n            .fjcontrols-control-text {\n                font-size: 0.9em;\n                cursor: auto;\n                padding-left: 1%;\n                padding-right: 1%;\n                border-left: none;\n                border-right: none;\n            }\n            \n            .fjcontrols-control-text-small {\n                font-size: 1em;\n                line-height: 2;\n            }\n            \n            .hprogressbar {\n                width: 99%;\n                left: 0.5%;\n                text-align: center;\n                display: inline-block;\n            }\n            \n            .vprogressbar {\n                display: none;\n                width: 15%;\n                border-radius: 5px;\n                padding: 1px;\n                margin-left: 80%;\n                margin-right: 5%;\n                margin-top: 1%;\n      